}
.navbar {
    top: 0;
    position: fixed;
    width: 100%;
    padding: 20px 0;
    background-color: rgba(210,233,255,0.9);
    color: #111111;
    font-weight: 500;
    font-size: 15px;
    transition: all 0.3s ease-in-out;
    z-index: 999;
}

.navbar .container {
    display: flex;
    box-sizing: border-box; /* Important to preserve padding */
    padding: 0 20px;
    height: 50px;
    width: 100%;
    max-width: 1170px;
    margin-left: auto;
    margin-right: auto;
    align-items: center;
}    

.scrolled {
    padding-top: 5px;
    padding-bottom: 5px;
}

.navbar .menu-btn {
    display: none;
    color: #777777;
    font-size: 25px;
}

.navbar .menu {
    display: flex;
}

.button#navbar-btn {
    margin: 0;
    padding: 5px 10px;
    border-radius: 3px;
    font-weight: 500;
    box-shadow: none;
}

.button#navbar-btn:hover {
    background-color: #7575DA;
    color: #ffffff;
}

.navbar a {
    transition: all 0.3s ease-in-out;
}

.navbar a:hover {
    color: #6138fa;
}

.navbar img {
    margin-right: auto;
}

.navbar ul {
    display: flex;
    list-style-type: none;
    align-items: center;
}

.navbar li {
    padding: 0px 14px;
}

@media only screen and (max-width: 1024px) {
    .navbar {
        position: static;
    }

    /* disabling animation */
    .scrolled {
        padding-top: 20px;
        padding-bottom: 20px;
    }

    /* making the navbar a bit narrower */
    .navbar .container {
        padding: 0 40px;
    }

    /* showing button */
    .navbar .menu-btn {
        display: block;
    }

    /* hiding menu */
    .navbar .menu {
        display: none;
    }

    .navbar ul {
        padding: 0;
        margin: 0;
    }

    .navbar li {
        padding: 10px 40px;
        border-top: solid 1px #222222;
    }
    
    .navbar .menu-open {
        display: block;
        position: absolute;
        top: 70px;
        right: 40px;
        background-color: #111111;
        color: #ffffff;
    }
    
    .navbar .menu-open ul {
        display: block;
    }

    .navbar li#btn {
        background-color: #7575DA;
        transition: all 0.3s ease-in-out;
    }

    .navbar li#btn:hover {
        background-color: #393689;
    }

    .navbar li#btn .button#navbar-btn {
        margin: 0;
        padding: 0;
        border-radius: 3px;
        font-weight: 500;
        box-shadow: none;
        background-color: #7575DA;
        color: #ffffff;
    }
    
    .navbar li#btn:hover .button#navbar-btn {
        background-color: #393689;
        color: #ffffff;
    }

    .navbar a:hover {
        color: #aaaaaa;
    }
}

@media only screen and (max-width: 767px) {
    .navbar .container {
        display: block;
        padding: 0;
        text-align: center;
    }

    .navbar .menu-open {
        display: block;
        position: relative;
        top: 0;
        right: 0;
        background-color: #111111;
        color: #ffffff;
    }
}

.go-top {
    position: fixed;
    right: 20px;
    bottom: -20px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 1em;
    height: 1em;
    border-radius: 50%;
    padding: 10px;
    background-color: #7575da;
    color: #FFFFFF;
    font-size: 16px;
    transition: all 0.3s;
    visibility: hidden;
    opacity: 0;
    cursor: pointer;
}

.go-top.showing {
    bottom: 20px;
    visibility: visible;
    opacity: 1;
}

.go-top:hover {
    background-color: transparent;
    color: #7575da;
}
